:root {
  --red: #dc3545;
  --dark: #343a40;
  --black: #000;
  --white: #ffffff;
  --whitesmoke: #f2f2f2;
  --lightgrey: #d3d3d3;
}
body,
html {
  scroll-behavior: smooth;
}
/* .home,
.aboutus,
.services,
.portfolio {
  padding-top: 70px;
} */
body {
  padding-top: 70px;
}
.slider {
  position: relative;
  height: 90vh;
}
.aboutus .slider {
  height: auto;
}
@media screen and (min-width: 300px) and (max-width: 778px) {
  .slider {
    height: auto;
  }
}
.slide-content {
  position: absolute;
  top: 40%;
}
.slide-content h3 {
  animation: move 1s;
}
.slide-content p {
  animation: move 1.5s;
}
.slide-content button {
  animation: move 2s;
}
.content-img {
  position: absolute;
  width: 350px;
  bottom: 0;
  right: 20%;
  animation: moveright 2.2s;
}
@keyframes move {
  0% {
    transform: translateY(600px);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes moveright {
  0% {
    transform: translateX(600px);
  }
  100% {
    transform: translateX(0);
  }
}
.nav-link:hover {
  background-color: var(--red) !important;
}
.nav-link:focus {
  background-color: var(--red) !important;
}
.dropdown-item {
  background-color: var(--dark) !important;
  color: var(--white) !important;
}
.dropdown-item:hover {
  background-color: var(--red) !important;
}

.carousel-indicators li {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 3px solid var(--dark);
}
.heading h1,
.heading p {
  font-family: serif;
}
.features {
  background-color: var(--whitesmoke);
}

.icon i {
  width: 110px;
  height: 110px;
  line-height: 90px;
  text-align: center;
  border-radius: 50%;
  font-size: 2.5rem;
  font-weight: bold;
  color: var(--red);
  background-color: var(--white);
  border: 10px double var(--whitesmoke);
  transition: background-color, border 1s ease-in;
}
.icon i:hover {
  color: var(--white);
  background-color: var(--red);
  /* border: 10px double var(--red); */
  border: 10px solid var(--red);
}
.workbox {
  position: relative;
  width: fit-content;
  height: auto;
}
.workbox-content {
  position: absolute;
  top: 0;
  padding: 30px 20px;
  color: transparent;
  transition: background-color 1s ease;
}

.workbox:hover .workbox-content {
  background-color: var(--red);
  color: var(--white);
}
.ourservices {
  padding: 50px 0;
  background-image: linear-gradient(rgba(0, 0, 0, 0.685), rgba(0, 0, 0, 0.685)),
    url(./images/services/bg_services.png);
  background-size: cover;
}
.ourskills {
  background-color: var(--whitesmoke);
}

.accordion .card-header {
  padding: 0;
}

.side-acc1,
.side-acc2,
.side-acc3,
.side-acc4,
.side-acc5 {
  position: relative;
}
/* .side-acc1,
.side-acc2,
.side-acc3,
.side-acc4,
.side-acc5 {
  transition: background-color 1s ease-in;
}
 */
.side-acc1:hover,
.side-acc2:hover,
.side-acc3:hover,
.side-acc4:hover,
.side-acc5:hover {
  background-color: var(--black);
  color: var(--white);
}
.side-acc1::before,
.side-acc2::before,
.side-acc3::before,
.side-acc4::before,
.side-acc5::before {
  content: "";
  display: none;
  position: absolute;
  width: auto;
  height: auto;
  top: 0;
  right: -53px;
  border-style: solid;
  border-color: transparent transparent transparent var(--black);
  border-width: 28px 27px;
  z-index: 100;
}
.side-acc1:hover.side-acc1::before,
.side-acc2:hover.side-acc2::before,
.side-acc3:hover.side-acc3::before,
.side-acc4:hover.side-acc4::before,
.side-acc5:hover.side-acc5::before {
  display: block;
}
.pull-left {
  border-left: 1px solid var(--dark);
  padding-left: 25px;
}
.pull-left:before {
  content: "";
  position: absolute;
  width: 9px;
  height: 9px;
  top: 30%;
  left: 11px;
  margin-top: -5px;
  background: #fff;
  transform: rotate(45deg);
  border: 1px solid var(--dark);
  /* right: -5px; */
  border-left: 0 none;
  border-bottom: 0 none;
}
.ourpartners {
  background-image: url(./images//partners/partner_bg.png);
  background-size: cover;
}
