.roundskill {
  position: relative;
  width: 200px;
  height: 200px;
  background-color: var(--lightgrey);
  border-radius: 50%;
  overflow: hidden;
}
.round-color {
  position: absolute;
  bottom: 0;
  width: 100%;
}
.round-content {
  position: absolute;
  width: 100%;
  height: 100%;
  color: var(--white);
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  padding: 20px;
}
.fa-facebook,
.fa-twitter,
.fa-google-plus {
  width: 30px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  border-radius: 50%;
  color: var(--white);
}
.fa-facebook {
  background-color: rgba(61, 61, 209, 0.932);
}
.fa-twitter {
  background-color: rgb(20, 167, 224);
}
.fa-google-plus {
  background-color: var(--red);
}
@media screen and (min-width: 992px) {
  .time-code i {
    width: 30px;
    height: 30px;
    margin: 0 5px;
    text-align: center;
    line-height: 30px;
    border-radius: 50%;
    color: var(--white);
    font-weight: bold;
  }
  .time-code hr {
    height: 1.5px;
  }
  .timeline1 {
    margin-left: 30px;
  }
  .timeline2 {
    margin-left: 160px;
  }
  .timeline3 {
    margin-right: 160px;
  }
  .timeline1::before,
  .timeline2::before {
    content: "";
    position: absolute;
    width: 10px;
    height: 10px;
    bottom: 19px;
    left: 50px;
    background: #fff;
    transform: rotate(135deg);
    border: 1px solid var(--lightgrey);
    border-left: 0 none;
    border-bottom: 0 none;
  }
  .timeline3::before,
  .timeline4::before {
    content: "";
    position: absolute;
    width: 10px;
    height: 10px;
    top: 19px;
    left: 50px;
    background: #fff;
    transform: rotate(-45deg);
    border: 1px solid var(--lightgrey);
    border-left: 0 none;
    border-bottom: 0 none;
  }
}
.readytoget {
  position: relative;
  background-color: var(--whitesmoke);
  margin: 50px auto;
  /* padding: 10px 0; */
}
.readytoget button {
  position: absolute;
  bottom: -13px;
  left: 0;
  right: 0;
  margin: 0 auto;
}
.readytoget button::before {
  content: "";
  position: absolute;
  border-width: 10px 9px;
  border-style: solid;
  border-color: transparent transparent var(--red) transparent;
  transform: rotate(45deg);
  left: -8px;
  bottom: 3px;
}
.readytoget button::after {
  content: "";
  position: absolute;
  border-width: 10px 9px;
  border-style: solid;
  border-color: transparent var(--red) transparent transparent;
  transform: rotate(45deg);
  right: -8px;
  bottom: 2px;
}

.client-img {
  border: 5px solid var(--lightgrey);
  border-radius: 50%;
}
.main-nav:hover {
  background-color: var(--red);
  color: var(--white);
}
.main-nav-active {
  background-color: var(--red);
  color: var(--white);
}
.search_box {
  background-image: url(../src/images/search_icon.png);
  background-position: 95%;
  background-repeat: no-repeat;
  border-color: #dedede;
  height: 48px;
  outline: medium none;
  box-shadow: none;
}

.blog-category button:hover {
  background-color: var(--red);
  color: var(--white);
}
.blog-category .badge {
  position: relative;
  bottom: 20px;
  left: 20px;
  padding: 5px;
}
.blog-category .badge::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 7px;
  border-width: 10px 12px;
  border-style: solid;
  border-color: transparent transparent transparent var(--red);
  z-index: -15;
}
.tagcloud button {
  background-color: var(--whitesmoke);
}
.tagcloud button:hover {
  background-color: var(--red);
  color: var(--white);
}
.blog-left div {
  border-bottom: 5px solid var(--black);
}
.blog-left button {
  background-color: var(--whitesmoke);
  margin: 0;
}
.map {
  width: 250px;
  height: 250px;
  border: 10px solid var(--whitesmoke);
}
.contactbg {
  background-image: url(../src/images/map.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  padding: 50px 0;
}

.required::after {
  content: "*";
  position: absolute;
  color: var(--red);
  font-weight: bold;
}
